<!-- 基础直方图
 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>基础直方图</h3>
      <div id="container"></div>
    </div>
    <div class="box">
      <h3>直方图范围刻度</h3>
      <div id="containerG2"></div>
    </div>
  </div>
</template>

<script>
import { Histogram } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {
      data: [
        { value: 1.2 },
        { value: 3.4 },
        { value: 3.7 },
        { value: 4.3 },
        { value: 5.2 },
        { value: 5.8 },
        { value: 6.1 },
        { value: 6.5 },
        { value: 6.8 },
        { value: 7.1 },
        { value: 7.3 },
        { value: 7.7 },
        { value: 8.3 },
        { value: 8.6 },
        { value: 8.8 },
        { value: 9.1 },
        { value: 9.2 },
        { value: 9.4 },
        { value: 9.5 },
        { value: 9.7 },
        { value: 10.5 },
        { value: 10.7 },
        { value: 10.8 },
        { value: 11.0 },
        { value: 11.0 },
        { value: 11.1 },
        { value: 11.2 },
        { value: 11.3 },
        { value: 11.4 },
        { value: 11.4 },
        { value: 11.7 },
        { value: 12.0 },
        { value: 12.9 },
        { value: 12.9 },
        { value: 13.3 },
        { value: 13.7 },
        { value: 13.8 },
        { value: 13.9 },
        { value: 14.0 },
        { value: 14.2 },
        { value: 14.5 },
        { value: 15 },
        { value: 15.2 },
        { value: 15.6 },
        { value: 16.0 },
        { value: 16.3 },
        { value: 17.3 },
        { value: 17.5 },
        { value: 17.9 },
        { value: 18.0 },
        { value: 18.0 },
        { value: 20.6 },
        { value: 21 },
        { value: 23.4 }
      ]
    }
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = this.data
    const histogramPlot = new Histogram('container', {
      data,
      binField: 'value',
      binWidth: 2
    })

    histogramPlot.render()

    this.getTwo()
  },
  methods: {
    getTwo() {
      const data = [
        { value: 1.2 },
        { value: 3.4 },
        { value: 3.7 },
        { value: 4.3 },
        { value: 5.2 },
        { value: 5.8 },
        { value: 6.1 },
        { value: 6.5 },
        { value: 6.8 },
        { value: 7.1 },
        { value: 7.3 },
        { value: 7.7 },
        { value: 8.3 },
        { value: 8.6 },
        { value: 8.8 },
        { value: 9.1 },
        { value: 9.2 },
        { value: 9.4 },
        { value: 9.5 },
        { value: 9.7 },
        { value: 10.5 },
        { value: 10.7 },
        { value: 10.8 },
        { value: 11.0 },
        { value: 11.0 },
        { value: 11.1 },
        { value: 11.2 },
        { value: 11.3 },
        { value: 11.4 },
        { value: 11.4 },
        { value: 11.7 },
        { value: 12.0 },
        { value: 12.9 },
        { value: 12.9 },
        { value: 13.3 },
        { value: 13.7 },
        { value: 13.8 },
        { value: 13.9 },
        { value: 14.0 },
        { value: 14.2 },
        { value: 14.5 },
        { value: 15 },
        { value: 15.2 },
        { value: 15.6 },
        { value: 16.0 },
        { value: 16.3 },
        { value: 17.3 },
        { value: 17.5 },
        { value: 17.9 },
        { value: 18.0 },
        { value: 18.0 },
        { value: 20.6 },
        { value: 21 },
        { value: 23.4 }
      ]

      const histogramPlot = new Histogram('containerG2', {
        data,
        binField: 'value',
        binWidth: 4,
        tooltip: {
          showMarkers: false,
          position: 'top'
        },
        interactions: [
          {
            type: 'element-highlight'
          }
        ],
        /** range 为 x 轴代表字段，count 为 y 轴统计个数字段 */
        meta: {
          range: {
            min: 0,
            tickInterval: 2
          },
          count: {
            max: 20,
            nice: true
          }
        }
      })

      histogramPlot.render()
    }
  },
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



